/* Buttons styles */
input::-moz-focus-inner,
Button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input[type="submit"].btn,
Button.btn {
  cursor: pointer;
}

Button.btn,.btn {
  display: inline-block;
  outline: none;
  *zoom: 1;
  text-align: center;
  text-decoration: none;
  font-family: inherit;
  font-weight: 600;
  letter-spacing: 1px;
  vertical-align: middle;
  border: 1px solid;
  transition: all 0.2s ease;
  box-sizing: border-box;
  text-shadow: 0 1px 0 rgba(0,0,0,0.01);
	border: 2px solid rgb(209, 29, 50);
}
/* Radius */
.btn-radius {
margin-top:10px;
  border-radius: 8px;
  padding: 8px 50px 30px 58px ;
  border-radius: 8px;
  height: 60px;
  outline: none;
  cursor: pointer;
  margin-top: 0.625rem;
  max-width: 180px;
  border-color: rgb(209, 29, 50);
  color: #000;
}
.btn-radius:hover {
 border-radius: 8px;
  height: 60px;
  outline: none;
  cursor: pointer;
  margin-top: 0.625rem;
  max-width: 180px;
  background-color: rgb(209, 29, 50);
  color: #FFF;
  
}

